@font-face {
	font-family: statefaceregular;
	src: url(stateface-regular-webfont.eot);
	src: url(stateface-regular-webfont.eot?#iefix) format('embedded-opentype'), url(stateface-regular-webfont.woff) format('woff'), url(stateface-regular-webfont.ttf) format('truetype'), url(stateface-regular-webfont.svg#StateFaceRegular) format('svg');
	font-weight: 400;
	font-style: normal
}

.stateface {
	font-family: statefaceregular;
	display: inline-block;
	speak: none;
	font-style: normal;
	font-weight: 400;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale
}

